@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@400;600&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

body {
  background-color: darkblue;
  color: bisque;   
  font-family: 'Rubik', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  max-width: 1100px;
  margin: 0 auto;
}

a {
  color: aliceblue;
}


.App{padding:20px;text-align:left}.won-card{background-color:#c559c5}.lost-card{background-color:crimson}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:20px;margin-bottom:1px;margin-top:2px;border-radius:12px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .additional-detail{text-align:right}.MatchDetailCard .additional-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px;border-radius:12px}.match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-rows:1fr 1fr 1fr;grid-template-columns:1fr 1fr 1fr 1fr;grid-row-gap:10px;grid-column-gap:10px}.TeamPage .team-name-section{grid-column:span 3;justify-self:start;align-self:flex-end}.TeamPage .team-name{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss-section{justify-self:center;padding:20px}.TeamPage .match-detail-section{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-bottom:12px;margin-top:16px}

.MatchPage{display:grid;grid-template-columns:1fr 4fr;grid-column-gap:90px}.MatchPage .year-selector{justify-self:end;padding:20px}.match-heading{margin-bottom:14px}

.HomePage .app-name{font-size:2.5rem;margin:30px auto;text-align:center}.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:12px;grid-row-gap:12px}

.TeamTile{height:150px;text-align:center;border:1px solid orchid;padding:20px}

